	module.exports = {"data":{"markdownRemark":{"html":"<h3>Newly Designed Program Profile Page</h3>\n<p>We've redesigned the program profile pages to give them a sleeker look.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/july_2019_program_page-3fb2b17e18737f01b1715f44c5a503b1-bfe0b.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 67.00302724520687%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Revamped program profile page\"\n        title=\"\"\n        src=\"/static/july_2019_program_page-3fb2b17e18737f01b1715f44c5a503b1-2f183.png\"\n        srcset=\"/static/july_2019_program_page-3fb2b17e18737f01b1715f44c5a503b1-67829.png 125w,\n/static/july_2019_program_page-3fb2b17e18737f01b1715f44c5a503b1-5c59f.png 250w,\n/static/july_2019_program_page-3fb2b17e18737f01b1715f44c5a503b1-2f183.png 500w,\n/static/july_2019_program_page-3fb2b17e18737f01b1715f44c5a503b1-c93e8.png 750w,\n/static/july_2019_program_page-3fb2b17e18737f01b1715f44c5a503b1-4e628.png 1000w,\n/static/july_2019_program_page-3fb2b17e18737f01b1715f44c5a503b1-b3ac4.png 1500w,\n/static/july_2019_program_page-3fb2b17e18737f01b1715f44c5a503b1-bfe0b.png 1982w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Link Burp Suite with the HackerOne Scope</h3>\n<p>It can take a long time to set up Target Scope in Burp Suite, especially for programs with long or complicated scopes. Now you can download a Burp Suite project file to link HackerOne scope to the Burp Suite Target Scope.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/july_2019_download_burp_suite_file-b93ee76a15228a273f21da2a597cc136-3a650.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 70.20348837209302%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"link to download burp suite file\"\n        title=\"\"\n        src=\"/static/july_2019_download_burp_suite_file-b93ee76a15228a273f21da2a597cc136-2f183.png\"\n        srcset=\"/static/july_2019_download_burp_suite_file-b93ee76a15228a273f21da2a597cc136-67829.png 125w,\n/static/july_2019_download_burp_suite_file-b93ee76a15228a273f21da2a597cc136-5c59f.png 250w,\n/static/july_2019_download_burp_suite_file-b93ee76a15228a273f21da2a597cc136-2f183.png 500w,\n/static/july_2019_download_burp_suite_file-b93ee76a15228a273f21da2a597cc136-c93e8.png 750w,\n/static/july_2019_download_burp_suite_file-b93ee76a15228a273f21da2a597cc136-4e628.png 1000w,\n/static/july_2019_download_burp_suite_file-b93ee76a15228a273f21da2a597cc136-3a650.png 1376w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Bounty Eligibility Indicator on Structured Scopes</h3>\n<p>We've updated the structured scopes table on the program policy page with a new bounty eligible and ineligible icon to clearly show which assets are eligible and ineligible for bounties.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/july_2019_bounty_eligibility_icon-9f09ace448672e22705653fdaed5b1a4-8edeb.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 58.98268398268398%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"bounty eligibility indicator\"\n        title=\"\"\n        src=\"/static/july_2019_bounty_eligibility_icon-9f09ace448672e22705653fdaed5b1a4-2f183.png\"\n        srcset=\"/static/july_2019_bounty_eligibility_icon-9f09ace448672e22705653fdaed5b1a4-67829.png 125w,\n/static/july_2019_bounty_eligibility_icon-9f09ace448672e22705653fdaed5b1a4-5c59f.png 250w,\n/static/july_2019_bounty_eligibility_icon-9f09ace448672e22705653fdaed5b1a4-2f183.png 500w,\n/static/july_2019_bounty_eligibility_icon-9f09ace448672e22705653fdaed5b1a4-c93e8.png 750w,\n/static/july_2019_bounty_eligibility_icon-9f09ace448672e22705653fdaed5b1a4-8edeb.png 924w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Internal Attachments</h3>\n<p>You can convert attachments to be internal for all redactable reports in the <strong>Convert attachments to internal-only</strong> section when redacting a report.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/july_2019_convert_attachments_internal-7987a02f0b4be27a2655a0b27aad21de-2f286.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 63.91608391608392%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"convert attachments to internal-only\"\n        title=\"\"\n        src=\"/static/july_2019_convert_attachments_internal-7987a02f0b4be27a2655a0b27aad21de-2f183.png\"\n        srcset=\"/static/july_2019_convert_attachments_internal-7987a02f0b4be27a2655a0b27aad21de-67829.png 125w,\n/static/july_2019_convert_attachments_internal-7987a02f0b4be27a2655a0b27aad21de-5c59f.png 250w,\n/static/july_2019_convert_attachments_internal-7987a02f0b4be27a2655a0b27aad21de-2f183.png 500w,\n/static/july_2019_convert_attachments_internal-7987a02f0b4be27a2655a0b27aad21de-c93e8.png 750w,\n/static/july_2019_convert_attachments_internal-7987a02f0b4be27a2655a0b27aad21de-4e628.png 1000w,\n/static/july_2019_convert_attachments_internal-7987a02f0b4be27a2655a0b27aad21de-2f286.png 1430w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Program Notifications</h3>\n<p>After the beta launch in March, all hackers can now subscribe to receive notifications from program updates.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/mar_2019_program_notifications-2-bffa167ea10455349f7c5f041e91e5cf-1007a.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 14.565826330532214%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAADCAYAAACTWi8uAAAACXBIWXMAAAsSAAALEgHS3X78AAAAcklEQVQI142Oyw7CMBAD8/+fyIkLFA5NWnY3Jc8hrVSJYy2NbPlg2THUe79MS5kShBa/tFyopVLrYGQzw3nvCSGw+z97d/anqyo20OWD3CfiNJO9kt4reRZsFZyIcJVjcLywaOgW2RahPALp9iI/wzH4A9R05+uYkdF0AAAAAElFTkSuQmCC'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"subscribe button\"\n        title=\"\"\n        src=\"/static/mar_2019_program_notifications-2-bffa167ea10455349f7c5f041e91e5cf-2f183.png\"\n        srcset=\"/static/mar_2019_program_notifications-2-bffa167ea10455349f7c5f041e91e5cf-67829.png 125w,\n/static/mar_2019_program_notifications-2-bffa167ea10455349f7c5f041e91e5cf-5c59f.png 250w,\n/static/mar_2019_program_notifications-2-bffa167ea10455349f7c5f041e91e5cf-2f183.png 500w,\n/static/mar_2019_program_notifications-2-bffa167ea10455349f7c5f041e91e5cf-1007a.png 714w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h4>Notification Preferences</h4>\n<p>You can also now set your notification preferences under the new <strong>Notification Preferences</strong> page in your user settings.  </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/july_2019_notification_preferences-c5db2e823f5b08778632ed2ac8ef9922-487bd.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 79.56698240866035%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Notification Preferences\"\n        title=\"\"\n        src=\"/static/july_2019_notification_preferences-c5db2e823f5b08778632ed2ac8ef9922-2f183.png\"\n        srcset=\"/static/july_2019_notification_preferences-c5db2e823f5b08778632ed2ac8ef9922-67829.png 125w,\n/static/july_2019_notification_preferences-c5db2e823f5b08778632ed2ac8ef9922-5c59f.png 250w,\n/static/july_2019_notification_preferences-c5db2e823f5b08778632ed2ac8ef9922-2f183.png 500w,\n/static/july_2019_notification_preferences-c5db2e823f5b08778632ed2ac8ef9922-c93e8.png 750w,\n/static/july_2019_notification_preferences-c5db2e823f5b08778632ed2ac8ef9922-4e628.png 1000w,\n/static/july_2019_notification_preferences-c5db2e823f5b08778632ed2ac8ef9922-487bd.png 1478w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"path":"/changelog/2019/july","title":"July 2019"}}},"pathContext":{}}

